registerBlockType('contentfry/gutenberg-block', {
  title: __('Contentfry', 'contentfry'),
  icon: iconEl,
  description: 'Your contentfry display',
  category: 'layout',
  attributes: {
    accessToken: {
      type: 'string',
      default: null,
    },
    room: {
      type: 'number',
      default: null,
    },
    display: {
      type: 'number',
      default: null,
    },
    connection: {
      type: 'array',
      default: [],
    },
    source: {
      type: 'array',
      default: [],
    },
    story: {
      type: 'array',
      default: [],
    },
    limit: {
      type: 'limit',
      default: 5,
    },
    loadMore: {
      type: 'boolean',
      default: false,
    },
  },
  edit: props => {
    const {
      clientId,
      attributes,
    } = props;

    const onChangeTeam = (accessToken) => {
      props.setAttributes({ accessToken });
    };

    const onChangeRoom = (room) => {
      props.setAttributes({ room: Number(room) });
    };

    const onChangeDisplay = (display) => {
      props.setAttributes({ display: Number(display) });
    };

    const onChangeControls = (key, value) => {
      if ((key === 'connection' || key === 'source' || key === 'story') && value) {
        value = value.map(v => v.value);
      }

      props.setAttributes({
        [key]: value,
      });
    };

    const editView = (
      <Fragment>
        <ContentfryControls room={ attributes.room } accessToken={ attributes.accessToken } defaults={ { ...attributes } } onChange={ onChangeControls } />
        <ContentfryPreview id={ clientId } { ...attributes } />
      </Fragment>
    );

    const creatView = (
      <Placeholder icon={ iconEl } label="Contentfry" className="contentfry-placeholder">
        <p>Embed a Social Wall from contentfry</p>
        <ContentfryTeams label="Select Team" value={ attributes.accessToken } onChange={ onChangeTeam } />
        <ContentfryRooms label="Select Room" value={ attributes.room } onChange={ onChangeRoom } accessToken={ attributes.accessToken } />
        <ContentfryDisplays label="Select Display" value={ attributes.display } room={ attributes.room } accessToken={ attributes.accessToken } onChange={ onChangeDisplay } />
      </Placeholder>
    );

    return attributes.display ? editView : creatView;
  },
  save: props => {
    const { display, limit, connection, source, story, loadMore } = props.attributes;

    let config = `limit=${limit}&test=1`;

    if (connection) {
      config += `&connections=${connection.join(',')}`;
    }

    if (source) {
      config += `&sources=${source.join(',')}`;
    }

    if (story) {
      config += `&stories=${story.join(',')}`;
    }

    config += `&hide_load_more=${loadMore}`;

    return display ? <div className="contentfry-wrapper" data-cf-display-id={ display } data-cf-config={ config }></div> : null;
  },
});
