import axios from 'axios';
import { __ } from '@wordpress/i18n';
import { Component, Fragment } from '@wordpress/element';
import { BaseControl, PanelBody, ToggleControl, TextControl, SelectControl } from '@wordpress/components';
import { InspectorControls } from '@wordpress/editor';
import Select from 'react-select';

class ContentfryControls extends Component {
  constructor(props) {
    super(props);

    this.state = {
      accessToken: props.accessToken,
      room: props.room,
      displays: [],
      connections: [],
      sources: [],
      stories: [],
      tokens: [],
    };
  }

  async componentDidMount() {
    const { room, accessToken } = this.props;
    if (room && accessToken) {
      const data = await this.getData(room, accessToken);
      this.setState({ room, ...data });
    }
  }

  async getData(room, accessToken) {
    if (room && accessToken) {
      return axios.get(`https://graph.contentfry.com/v2.0/rooms/${room}`, {
        params: {
          access_token: accessToken,
        },
      }).then(response => response.data.data).then(data => {
        const displays = data.displays.map(display => ({ label: display.name, value: display.id }));
        const connections = data.connections.map(connection => ({ label: connection.name, value: connection.id }));
        const sources = data.sources.map(source => ({ label: source, value: source }));
        const stories = data.stories.map(story => ({ label: story.name, value: story.id }));
        const groups = [];
        const themes = [];

        return { displays, connections, sources, stories, groups, themes };
      });
    }
  }

  render() {
    const { displays, connections, sources, stories } = this.state;
    const { defaults, onChange } = this.props;

    return (
      <Fragment>
        <InspectorControls>
          <PanelBody title={ __('Settings', 'contentfry') }>

            <SelectControl
              label={ __('Display', 'contentfry') }
              value={ defaults.display }
              options={ displays }
              onChange={ (value) => onChange('display', Number(value)) }
            />

            <BaseControl
              label="Connections"
              help="Type to search a connection"
            >
              <Select
                label={ __('Connections', 'contentfry') }
                value={ connections.filter(c => defaults.connection && defaults.connection.indexOf(c.value) !== -1) }
                options={ connections }
                onChange={ (value) => onChange('connection', value) }
                isMulti="true"
                placeholder=""
              />
            </BaseControl>

            <BaseControl
              label="Sources"
              help="Type to search a source"
            >
              <Select
                label={ __('Sources', 'contentfry') }
                value={ sources.filter(item => defaults.source && defaults.source.indexOf(item.value) !== -1) }
                options={ sources }
                onChange={ (value) => onChange('source', value) }
                isMulti="true"
                placeholder=""
              />
            </BaseControl>

            <BaseControl
              label="Stories"
              help="Type to search a story"
            >
              <Select
                label={ __('Stories', 'contentfry') }
                value={ stories.filter(item => defaults.story && defaults.story.indexOf(item.value) !== -1) }
                options={ stories }
                onChange={ (value) => onChange('story', value) }
                isMulti="true"
                placeholder=""
              />
            </BaseControl>

            <ToggleControl
              label={ __('Hide Load More', 'contentfry') }
              checked={ defaults.loadMore }
              onChange={ () => onChange('loadMore', !defaults.loadMore) }
            />

            <TextControl
              label={ __('Limit', 'contentfry') }
              value={ defaults.limit }
              onChange={ (value) => onChange('limit', Number(value)) }
            />
          </PanelBody>
        </InspectorControls>
      </Fragment>
    );
  }
}

export default ContentfryControls;
