import { Component } from '@wordpress/element';
import { SelectControl } from '@wordpress/components';

import axios from 'axios';

class ContentfryRooms extends Component {
  constructor(props) {
    super(props);

    this.state = {
      message: '',
      accessToken: null,
      rooms: [],
    };
  }

  static getDerivedStateFromProps(props, state) {
    if (props.accessToken !== state.accessToken) {
      return {
        accessToken: props.accessToken,
      };
    }

    return null;
  }

  componentDidMount() {
    this.load();
  }

  componentDidUpdate(prevProps) {
    const { accessToken } = this.state;
    if (accessToken !== '' && accessToken !== prevProps.accessToken) {
      this.getDisplays(accessToken).then((rooms) => {
        this.setState({ rooms });
      });
    }
  }

  load() {
    const { accessToken } = this.state;

    if (accessToken) {
      this.loadDisplays();
    }
  }

  getDisplays(accessToken) {
    return axios.get('https://graph.contentfry.com/v2.0/rooms', {
      params: {
        access_token: accessToken,
      },
    }).then(response => response.data.data).then(data => {
      return data.map(room => ({ label: room.name, value: room.id }));
    });
  }

  loadDisplays() {
    const { accessToken } = this.state;

    axios.get('https://graph.contentfry.com/v2.0/rooms', {
      params: {
        access_token: accessToken,
      },
    }).then(response => response.data.data).then(data => {
      const rooms = data.map(room => ({ label: room.name, value: room.id }));
      if (rooms.length > 0) {
        this.setState({ rooms, message: '' });
      } else {
        this.setState({ message: 'You don’t have any rooms available yet. Please go to app.contentfry.com to create your first room.' });
      }
    });
  }

  render() {
    const { rooms, accessToken, message } = this.state;
    const { value, label, onChange } = this.props;

    if (accessToken) {
      return rooms.length > 0 ? (
        <SelectControl
          label={ label }
          value={ value }
          options={ [{ label: '', value: '' }, ...rooms] }
          onChange={ onChange }
        />
      ) : message;
    }

    return '';
  }
}

export default ContentfryRooms;
