import { Component } from '@wordpress/element';
import { SelectControl } from '@wordpress/components';

import axios from 'axios';

class ContentfryTeams extends Component {
  constructor(props) {
    super(props);

    this.state = {
      teams: [],
    };
  }

  componentDidMount() {
    this.loadTeams();
  }

  loadTeams() {
    axios.get(CF.apiUrl).then(response => response.data).then(data => { // eslint-disable-line
      const teams = data.map(team => {
        const label = team.name && team.name !== 'null' ? `${team.team_name} - ${team.name}` : team.team_name;
        return ({ label, value: team.token });
      });
      this.setState({ teams });
    });
  }

  render() {
    const { teams } = this.state;
    const { value, label, onChange } = this.props;

    return (
      <SelectControl
        label={ label }
        value={ value }
        options={ [{ label: '', value: '' }, ...teams] }
        onChange={ onChange }
      />
    );
  }
}

export default ContentfryTeams;
