import axios from 'axios';
import { Component, Fragment } from '@wordpress/element';
import { SelectControl } from '@wordpress/components';

class ContentfryDisplays extends Component {
  constructor(props) {
    super(props);

    this.state = {
      accessToken: props.accessToken,
      room: props.room,
      displays: [],
      connections: [],
      sources: [],
      stories: [],
    };
  }

  async componentDidMount() {
    const { room, accessToken } = this.props;
    if (room && accessToken) {
      const data = await this.getData(room, accessToken);
      this.setState({ room, ...data });
    }
  }

  async shouldComponentUpdate(nextProps, nextState) {
    if (nextProps.room !== nextState.room) {
      const data = await this.getData(nextProps.room, nextProps.accessToken);
      this.setState({ room: nextProps.room, ...data });
      return true;
    }

    return false;
  }

  async getData(room, accessToken) {
    if (room && accessToken) {
      return axios.get(`https://graph.contentfry.com/v2.0/rooms/${room}`, {
        params: {
          access_token: accessToken,
        },
      }).then(response => response.data.data).then(data => {
        const displays = data.displays.map(display => ({ label: display.name, value: display.id }));
        const connections = data.connections.map(connection => ({ label: connection.name, value: connection.id }));
        const sources = data.sources;
        const stories = data.displays.map(story => ({ label: story.name, value: story.id }));

        return { displays, connections, sources, stories };
      });
    }
  }

  render() {
    const { displays } = this.state;
    const { value, label, onChange } = this.props;

    return displays.length > 0 ? (
      <Fragment>
        <SelectControl
          label={ label }
          value={ value }
          options={ [{ label: '', value: '' }, ...displays] }
          onChange={ onChange }
        />
      </Fragment>
    ) : null;
  }
}

export default ContentfryDisplays;
