import { Component, Fragment } from '@wordpress/element';

class ContentfryPreview extends Component {
  constructor(props) {
    super(props);

    this.state = {};
  }

  componentDidMount() {
    this.loadEmbed();
  }

  shouldComponentUpdate(nextProps, nextState) {
    const difference = JSON.stringify(nextProps) !== JSON.stringify(nextState);

    if (difference) {
      this.loadEmbed();
      return true;
    }

    return false;
  }

  loadEmbed() {
    const { id: blockId } = this.props;
    const embedContainer = document.querySelector(`[data-block="${blockId}"]`).querySelector('.contentfry-wrapper');

    if (embedContainer) {
      embedContainer.innerHTML = '';
      window.cfLoader.init(embedContainer);
      this.setState(this.props);
    }
  }

  render() {
    const { display, limit, connection, source, story, loadMore } = this.props;

    let config = `limit=${limit}`;

    if (connection) {
      config += `&connections=${connection.join(',')}`;
    }

    if (source) {
      config += `&sources=${source.join(',')}`;
    }

    if (story) {
      config += `&stories=${story.join(',')}`;
    }

    config += `&hide_load_more=${loadMore}`;

    return (
      <Fragment>
        <div className="contentfry-display-wp">
          <div className="contentfry-wrapper" data-cf-display-id={ display } data-cf-config={ config }></div>
        </div>
      </Fragment>
    );
  }
}

export default ContentfryPreview;
